    <div class="d-flex justify-content-between mb-3">
      <div> <!-- Î≤ÑÌäº ÏôºÏ™Ω Ï†ïÎ†¨ --> </div>
      <div> <!-- Î≤ÑÌäº Ïò§Î•∏Ï™Ω Ï†ïÎ†¨ -->
        <button class="btn btn-warning me-2" @click="resetForm">Ï¥àÍ∏∞Ìôî</button>
        <button class="btn btn-success" @click="lnInsert">Ï†ÄÏû•</button>
      </div>
    </div> <!-- Ïö∞Ï∏° Î≤ÑÌäº Î™®Ïùå ÏòÅÏó≠ ÎÅù -->
    <!-- Ïö∞Ï∏° ÏÉÅÏÑ∏Î≥¥Í∏∞ ÏòÅÏó≠ ÏãúÏûë -->
    <div class="card p-3">
      <h4>ÎùºÏù∏ Îì±Î°ù</h4>

      <div>
        <div>
          <div class="row mb-4">
            <!-- ÎùºÏù∏Î≤àÌò∏ -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnNo" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏Î≤àÌò∏</label>
                <input id="lnNo" type="text" class="form-control" v-model="ln_no" readonly disabled />
              </div>
            </div>
            <!-- ÎùºÏù∏Î™Ö -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnNm" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏Î™Ö</label>
                <input id="lnNm" type="text" class="form-control" v-model="ln_nm" />
              </div>
            </div>
            <!-- Ï†úÌíàÎ™Ö -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="prdNm" class="form-label me-3" style="min-width: 100px;">Ï†úÌíàÎ™Ö</label>
                <div class="input-group">
                  <input id="prdNm" type="text" class="form-control" v-model="prd_nm" readonly
                    placeholder="Ï†úÌíàÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî." />
                  <button class="btn btn-outline-secondary" @click="openPrdModal">üîç</button>
                </div>
              </div>
            </div>
            <!-- ÏÇ¨Ïö©Ïó¨Î∂Ä -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="useYn" class="form-label me-3" style="min-width: 100px;">ÏÇ¨Ïö©Ïó¨Î∂Ä</label>
                <input id="useYn" type="text" class="form-control" value="Ïó¨" readonly disabled />
              </div>
            </div>
            <!-- ÎùºÏù∏ÏÉÅÌÉú -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnSts" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏ÏÉÅÌÉú</label>
                <input id="lnSts" type="text" class="form-control" value="ÎπÑÍ∞ÄÎèô" readonly disabled />
              </div>
            </div>
            <!-- ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="dlyQty" class="form-label me-3" style="min-width: 100px;">ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ</label>
                <input id="dlyQty" type="text" class="form-control" v-model="dly_avg_pdn_qty" />
              </div>
            </div>
          </div>
          <table class="table table-bordered text-center">
            <thead class="table-light">
              <tr class="small-text">
                <th style="font-weight: normal; width: 10%;">ÏàúÏÑú</th>
                <th style="font-weight: normal; width: 15%;">Í≥µÏ†ïÏΩîÎìú</th>
                <th style="font-weight: normal;">Í≥µÏ†ïÏΩîÎìúÎ™Ö</th>
                <th style="font-weight: normal; width: 20%;">ÏÑ§ÎπÑÎ≤àÌò∏</th>
                <th style="font-weight: normal;">ÏÑ§ÎπÑÎ™Ö</th>
              </tr>
            </thead>
            <tbody v-if="lnDtlRows && lnDtlRows.length > 0" class="small-text">
              <tr v-for="(row, index) in lnDtlRows" :key="'new-' + index">
                <td><input v-model="row.seq" type="text" class="form-control" readonly disabled /></td>
                <td><input v-model="row.proc_code_no" type="text" class="form-control" readonly disabled /></td>
                <td><input v-model="row.proc_code_nm" type="text" class="form-control" readonly disabled /></td>
