              </tr>
            </tbody>
            <tbody v-else class="small-text">
              <tr>
                <td colspan="5">Ï†úÌíàÏùÑ ÏÑ†ÌÉùÌïòÏó¨ Í≥µÏ†ïÏùÑ Ï∂îÍ∞ÄÌï¥Ï£ºÏÑ∏Ïöî.</td>
              </tr>
            </tbody>
