<script>
// AJAX Î™®Îìà
import axios from 'axios';

import CommonCodeFormat from '@/utils/useCommonCode.js'

import prdSelectModal from '@/views/modal/prdSelectModal.vue';
import eqpSelectModal from '@/views/modal/eqpSelectModal.vue';

export default {
  components: {
    prdSelectModal,
    eqpSelectModal,
  },
  data() {
    return {
      ln_no: '', // ÎùºÏù∏Î≤àÌò∏
      ln_nm: '', // ÎùºÏù∏Î™Ö
      prd_no: '', // Ï†úÌíàÎ≤àÌò∏
      prd_nm: '', // Ï†úÌíàÎ™Ö
      use_yn: 'f1',// ÏÇ¨Ïö©Ïó¨Î∂Ä
      ln_sts: 'l2',// ÎùºÏù∏ÏÉÅÌÉú
      dly_avg_pdn_qty: null,// ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ

      lnDtlRows: [],
      selectedRowIndex: null, // ÏÑ†ÌÉùÎêú ÌñâÏùò Ïù∏Îç±Ïä§

      showPrdModal: false, // Ï†úÌíà ÏÑ†ÌÉù Î™®Îã¨ Ï¥àÍ∏∞ÌôîÍ∞í = Îã´Ìûò
      prdList: [], // Ï†úÌíà Î¶¨Ïä§Ìä∏ (showPrdModalÏóêÏÑú ÏÇ¨Ïö©)
      showEqpModal: false, // ÏûêÏû¨ ÏÑ†ÌÉù Î™®Îã¨ Ï¥àÍ∏∞ÌôîÍ∞í = Îã´Ìûò
      eqpList: [], // ÏÑ§ÎπÑ Î¶¨Ïä§Ìä∏ (showEqpModalÏóêÏÑú ÏÇ¨Ïö©)
    };
  },
  watch: {
    // prd_noÍ∞Ä Î≥ÄÍ≤ΩÎê† Îïå getLnDtl Ïã§Ìñâ
    prd_no(newVal) {
      if (newVal) {
        this.getLnDtl();
      }
    },
  },
  created() {
    this.getLnNo();
  },
  methods: {
    // ÎùºÏù∏Î≤àÌò∏Î•º Î∞õÏïÑÏò§Îäî Ìï®Ïàò
    async getLnNo() {
      let result = await axios.get(`/api/lnNo`)
        .catch(err => console.log(err));
      // padStart = SQLÏóêÏÑúÏùò LPADÏôÄ ÎèôÏùºÌïú Í∏∞Îä•ÏùÑ Í∞ÄÏßÑ Î¨∏ÏûêÏó¥Ìï®Ïàò => .padStart(ÏõêÌïòÎäî Í∏∏Ïù¥, Ï±ÑÏö∞Î†§Îäî Î¨∏Ïûê)
      this.ln_no = 'LINE-' + (result.data[0].lnNo).padStart(3, '0');
    },
    // Ï¥àÍ∏∞Ìôî Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò
    resetForm() {
      this.lnDtlRows = [];
      this.ln_nm = ''; // ÎùºÏù∏Î™Ö
      this.prd_no = ''; // Ï†úÌíàÎ≤àÌò∏
      this.prd_nm = ''; // Ï†úÌíàÎ™Ö
      this.use_yn = 'f1';// ÏÇ¨Ïö©Ïó¨Î∂Ä
      this.ln_sts = 'l2';// ÎùºÏù∏ÏÉÅÌÉú
      this.dly_avg_pdn_qty = null;// ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ
    },
    // Ï†ÄÏû• Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò 
    async lnInsert() {
      // ÎùºÏù∏Î™Ö Í≤ÄÏ¶ù
      if (!this.ln_nm?.trim()) {
        alert('ÎùºÏù∏Î™ÖÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // Ï†úÌíà ÏÑ†ÌÉù Í≤ÄÏ¶ù
      if (!this.prd_no) {
        alert('Ï†úÌíàÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ Í≤ÄÏ¶ù
      if (!this.dly_avg_pdn_qty || isNaN(Number(this.dly_avg_pdn_qty))) {
        alert('ÏùºÌèâÍ∑†ÏÉùÏÇ∞ÎüâÏùÄ Ïà´ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // Í≥µÏ†ïÎ≥Ñ ÏÑ§ÎπÑ ÏÑ†ÌÉù Í≤ÄÏ¶ù
      if (!this.lnDtlRows || this.lnDtlRows.length === 0) {
        alert('Í≥µÏ†ïÏùÑ Ï∂îÍ∞ÄÌï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      for (let i = 0; i < this.lnDtlRows.length; i++) {
        if (!this.lnDtlRows[i].eqp_no) {
          alert(`${i + 1}Î≤àÏß∏ Í≥µÏ†ïÏùò ÏÑ§ÎπÑÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.`);
          return;
        }
      }
      // ÏÑúÎ≤ÑÏóê Ï†ÑÎã¨Ìï† Ï†ïÎ≥¥Î•º Í∞ùÏ≤¥Î°ú Îî∞Î°ú Íµ¨ÏÑ±
      let lnInfo = {
        ln_no: this.ln_no,
        ln_nm: this.ln_nm.trim(),
        prd_no: this.prd_no,
        dly_avg_pdn_qty: Number(this.dly_avg_pdn_qty)
      };

      let lnDtlInfoArray = [];
      for (let i = 0; i < this.lnDtlRows.length; i++) {
        lnDtlInfoArray[i] = {
          ln_dtl_no: this.ln_no + '-' + (i + 1).toString().padStart(2, '0'),
          ln_no: this.ln_no,
          proc_srl_no: this.lnDtlRows[i].proc_srl_no,
          eqp_no: this.lnDtlRows[i].eqp_no
        };
      };

      // axios Î™®ÎìàÏùÑ ÌôúÏö©Ìï¥ AJAXÌïòÎäî Í≤ΩÏö∞ POSTÏôÄ PUTÏùÄ ÎëêÎ≤àÏß∏ Îß§Í∞úÎ≥ÄÏàòÎ°ú ÏÑúÎ≤ÑÏóê Î≥¥ÎÇº Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÎã¨, ÏûêÎèôÏúºÎ°ú JSON Ï†ÅÏö©
      let result = await axios.post("/api/lnAndLnDtl", [lnInfo, lnDtlInfoArray])
        .catch(err => console.log(err));

      if (result.data.message == 'Îì±Î°ù ÏôÑÎ£å') {
        alert('Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.');
        this.$emit('ln-reload');
      } else {
        alert('Îì±Î°ùÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.\nÎç∞Ïù¥ÌÑ∞Î•º ÌôïÏù∏Ìï¥Î≥¥ÏÑ∏Ïöî.');
      };
      this.$emit("goToInfo", true);
    },
    // Ï†úÌíà ÏÑ†ÌÉù Î™®Îã¨ Ïó¥Í∏∞
    openPrdModal() {
      axios.get('/api/prd')
        .then(res => {
          this.prdList = res.data;
          this.showPrdModal = true;
        })
        .catch(err => {
          console.error('Ï†úÌíà Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®', err)
        })
    },
    handleSelectedPrd(selectedPrd) { // Ï†úÌíà ÏÑ†ÌÉù Î™®Îã¨ÏóêÏÑú ÏÑ†ÌÉùÎêú Ï†úÌíà
      this.prd_no = selectedPrd.prd_no; // Î™®Îã¨ÏóêÏÑú ÏÑ†ÌÉùÌïú Ï†úÌíàÏùò Î≤àÌò∏Î•º this.prd_noÏóê Ï¥àÍ∏∞Ìôî
      this.prd_nm = selectedPrd.prd_nm; // Î™®Îã¨ÏóêÏÑú ÏÑ†ÌÉùÌïú Ï†úÌíàÏùò Ïù¥Î¶ÑÏùÑ this.prd_nmÏóê Ï¥àÍ∏∞Ìôî
      // Î™®Îã¨ Îã´Í∏∞
      this.showPrdModal = false;
    },
    // Ï†úÌíà Î≤àÌò∏Î°ú Ï†úÌíàÍ≥µÏ†ïÌùêÎ¶ÑÎèÑ Ï°∞Ìöå => Ï°∞ÌöåÌïú Í≥µÏ†ïÌùêÎ¶ÑÎèÑÎ•º lnDtlRowsÏóêÏÑú ÏÇ¨Ïö©
    async getLnDtl() {
      try {
        const result = await axios.get(`/api/lnDtlList/${this.prd_no}`);
        this.lnDtlRows = result.data || []; // Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏúºÎ©¥ Îπà Î∞∞Ïó¥Î°ú Ï¥àÍ∏∞Ìôî
      } catch (err) {
        console.error('Í≥µÏ†ï ÌùêÎ¶ÑÎèÑ Ï°∞Ìöå Ïã§Ìå®', err);
        this.lnDtlRows = []; // ÏóêÎü¨ Î∞úÏÉù Ïãú Îπà Î∞∞Ïó¥Î°ú Ï¥àÍ∏∞Ìôî
      }
    },
    // ÏÑ§ÎπÑ ÏÑ†ÌÉù Î™®Îã¨ Ïó¥Í∏∞
    openEqpModal(index) {
      axios.get('/api/eqp')
        .then(res => {
          this.selectedRowIndex = index; // ÌÅ¥Î¶≠Ìïú Ïù∏Îç±Ïä§Î•º Ï†ÄÏû•
          this.eqpList = res.data;
          this.showEqpModal = true;
        })
        .catch(err => {
          console.error('ÏÑ§ÎπÑ Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®', err)
        })
    },
    handleSelectedEqp(selectedEqp) { // ÏÑ§ÎπÑ ÏÑ†ÌÉù Î™®Îã¨ÏóêÏÑú ÏÑ†ÌÉùÎêú ÏÑ§ÎπÑ
      if (this.selectedRowIndex !== null) {
        // ÌÅ¥Î¶≠Ìïú Ïù∏Îç±Ïä§Ïùò lnDtlRows ÏóÖÎç∞Ïù¥Ìä∏
        this.lnDtlRows[this.selectedRowIndex].eqp_no = selectedEqp.eqp_no;
        this.lnDtlRows[this.selectedRowIndex].eqp_nm = selectedEqp.eqp_nm;
      }
      this.showEqpModal = false;
    },
  }
};
</script>

