  <!-- Ïö∞Ï∏° ÏòÅÏó≠ ÏãúÏûë -->
  <div class="col-md-7">
    <!-- Ïö∞Ï∏° Î≤ÑÌäº Î™®Ïùå ÏòÅÏó≠ -->
    <div class="d-flex justify-content-between mb-3">
      <div> <!-- Î≤ÑÌäº ÏôºÏ™Ω Ï†ïÎ†¨ -->
        <button class="btn btn-primary me-2" @click="addLn">Îì±Î°ù</button>
        <button class="btn btn-danger" @click="deleteLn(ln_no)">ÏÇ≠Ï†ú</button>
      </div>
      <div> <!-- Î≤ÑÌäº Ïò§Î•∏Ï™Ω Ï†ïÎ†¨ -->
        <button class="btn btn-warning me-2" @click="resetForm">Ï¥àÍ∏∞Ìôî</button>
        <button class="btn btn-success" @click="saveLn">Ï†ÄÏû•</button>
      </div>
    </div> <!-- Ïö∞Ï∏° Î≤ÑÌäº Î™®Ïùå ÏòÅÏó≠ ÎÅù -->
    <!-- Ïö∞Ï∏° ÏÉÅÏÑ∏Î≥¥Í∏∞ ÏòÅÏó≠ ÏãúÏûë -->
    <div class="card p-3">
      <h4>ÏÉÅÏÑ∏ Î≥¥Í∏∞</h4>
      <div v-if="ln">
        <div>
          <div class="row mb-4">
            <!-- ÎùºÏù∏Î≤àÌò∏ -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnNo" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏Î≤àÌò∏</label>
                <input id="lnNo" type="text" class="form-control" v-model="ln_no" readonly disabled />
              </div>
            </div>
            <!-- ÎùºÏù∏Î™Ö -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnNm" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏Î™Ö</label>
                <input id="lnNm" type="text" class="form-control" v-model="ln_nm" />
              </div>
            </div>
            <!-- Ï†úÌíàÎ™Ö -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="prdNm" class="form-label me-3" style="min-width: 100px;">Ï†úÌíàÎ™Ö</label>
                <input id="prdNm" type="text" class="form-control" v-model="prd_nm" readonly disabled />
              </div>
            </div>
            <!-- ÏÇ¨Ïö©Ïó¨Î∂Ä -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="useYn" class="form-label me-3" style="min-width: 100px;">ÏÇ¨Ïö©Ïó¨Î∂Ä</label>
                <select id="useYn" class="form-select form-control" v-model="use_yn">
                  <option value="f1">Ïó¨</option>
                  <option value="f2">Î∂Ä</option>
                </select>
              </div>
            </div>
            <!-- ÎùºÏù∏ÏÉÅÌÉú -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="lnSts" class="form-label me-3" style="min-width: 100px;">ÎùºÏù∏ÏÉÅÌÉú</label>
                <select id="lnSts" class="form-select form-control" v-model="ln_sts">
                  <option value="l1">ÎπÑÍ∞ÄÎèô</option>
                  <option value="l2">ÎåÄÍ∏∞Ï§ë</option>
                  <option value="l3">Í≥µÏ†ïÏ§ë</option>
                  <option value="l4">Í≥µÏ†ïÏôÑÎ£å</option>
                  <option value="l5">ÏàòÎ¶¨Ï§ë</option>
                  <option value="l6">Ï†êÍ≤ÄÏ§ë</option>
                </select>
              </div>
            </div>
            <!-- ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ -->
            <div class="col-md-6 mb-3">
              <div class="d-flex align-items-center">
                <label for="dlyQty" class="form-label me-3" style="min-width: 100px;">ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ</label>
                <input id="dlyQty" type="text" class="form-control" v-model="dly_avg_pdn_qty" />
              </div>
            </div>
          </div>
          <table class="table table-bordered text-center">
            <thead class="table-light">
              <tr class="small-text">
                <th style="font-weight: normal; width: 10%;">ÏàúÏÑú</th>
                <th style="font-weight: normal; width: 15%;">Í≥µÏ†ïÏΩîÎìú</th>
                <th style="font-weight: normal;">Í≥µÏ†ïÏΩîÎìúÎ™Ö</th>
                <th style="font-weight: normal; width: 20%;">ÏÑ§ÎπÑÎ≤àÌò∏</th>
                <th style="font-weight: normal;">ÏÑ§ÎπÑÎ™Ö</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(row, index) in lnDtlRows" :key="'new-' + index">
                <td><input v-model="row.seq" type="text" class="form-control" readonly disabled /></td>
                <td><input v-model="row.proc_code_no" type="text" class="form-control" readonly disabled /></td>
                <td><input v-model="row.proc_code_nm" type="text" class="form-control" readonly disabled /></td>
                <td>
                  <div class="input-group"><input v-model="row.eqp_no" type="text" class="form-control" readonly />
                    <button class="btn btn-outline-secondary" @click="openEqpModal(index)">üîç</button>
                  </div>
                </td>
                <td><input v-model="row.eqp_nm" type="text" class="form-control" readonly disabled /></td>
              </tr>
            </tbody>
          </table>

          <eqpSelectModal v-if="showEqpModal" :eqpList="eqpList" :selected="lnDtlRows" @select-eqp="handleSelectedEqp"
            @close="showEqpModal = false" />

        </div>
      </div>
      <div v-else> <!-- Î¶¨Ïä§Ìä∏ÏóêÏÑú ÏÑ†ÌÉùÎêú Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏùÑ Îïå -->
        <p>ÎùºÏù∏ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî!</p>
      </div>
    </div> <!-- Ïö∞Ï∏° ÏÉÅÏÑ∏Î≥¥Í∏∞ ÏòÅÏó≠ ÎÅù -->
  </div>
</template>

<script>
// AJAX Î™®Îìà
import axios from 'axios';
import eqpSelectModal from '@/views/modal/eqpSelectModal.vue';

export default {
  props: {
    ln: Object,
  },
  components: {
    eqpSelectModal,
  },
  data() {
    return {
      ln_no: '', // ÎùºÏù∏Î≤àÌò∏
      ln_nm: '', // ÎùºÏù∏Î™Ö
      prd_no: '', // Ï†úÌíàÎ≤àÌò∏
      prd_nm: '', // Ï†úÌíàÎ™Ö
      ln_sts: '',// ÎùºÏù∏ÏÉÅÌÉú
      use_yn: '',// ÏÇ¨Ïö©Ïó¨Î∂Ä
      dly_avg_pdn_qty: '',// ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ

      lnDtlRows: [],
      selectedRowIndex: null, // ÏÑ†ÌÉùÎêú ÌñâÏùò Ïù∏Îç±Ïä§

      showEqpModal: false, // ÏûêÏû¨ ÏÑ†ÌÉù Î™®Îã¨ Ï¥àÍ∏∞ÌôîÍ∞í = Îã´Ìûò
      eqpList: [], // ÏÑ§ÎπÑ Î¶¨Ïä§Ìä∏ (showEqpModalÏóêÏÑú ÏÇ¨Ïö©)
    };
  },
  watch: { // propsÎ°ú Î∞õÏùÄ ln Í∞ùÏ≤¥Ïùò Î≥ÄÌôîÎ•º Í∞êÏãú(watch)ÌïòÎäî Î∂ÄÎ∂Ñ
    // watchÎäî ÌäπÏ†ï Îç∞Ïù¥ÌÑ∞Ïùò Î≥ÄÌôîÎ•º Í∞êÏãú
    // handlerÎäî Í∑∏ Í∞êÏãúÌïòÎçò Îç∞Ïù¥ÌÑ∞Í∞Ä Î≥ÄÍ≤ΩÎê† Îïå Ïã§ÌñâÎêòÎäî Ìï®Ïàò(ÏΩúÎ∞± Ìï®Ïàò)
    ln: { // ln propsÏùò Î≥ÄÌôîÎ•º Í∞êÏãú, propsÎ°ú Î∞õÏùÄ ln Í∞íÏù¥ Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ Ïã§Ìñâ
      handler(newVal) { // ln Í∞íÏù¥ Î≥ÄÍ≤ΩÎê† Îïå Ïã§ÌñâÎêòÎäî Ìï®Ïàò, newValÏùÄ Î≥ÄÍ≤ΩÎêú ÏÉàÎ°úÏö¥ ln Í∞í
        if (newVal) {
          this.getLnDtlList(newVal.ln_no);
        } else {
          this.ln_no = '';
          this.ln_nm = '';
          this.prd_no = '';
          this.prd_nm = '';
          this.ln_sts = '';
          this.use_yn = '';
          this.dly_avg_pdn_qty = '';
          this.lnDtlRows = [];
        }
      },
      immediate: true //Ïù¥ ÏòµÏÖòÏù¥ ÏûàÏúºÎ©¥ Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä Ï≤òÏùå ÏÉùÏÑ±Îê† ÎïåÎèÑ watch Ìï∏Îì§Îü¨Î•º Ï¶âÏãú Ïã§Ìñâ
    }
  },
  methods: {
    // ln_noÎ•º Î∞õÏïÑ Îç∞Ïù¥ÌÑ∞ Î∞õÏïÑÏò§Îäî Ìï®Ïàò
    async getLnDtlList(selected) {
      let result = await axios.get(`/api/ln/${selected}`)
        .catch(err => console.log(err));
      this.lnDtlRows = result.data;
      this.ln_no = result.data[0].ln_no;
      this.ln_nm = result.data[0].ln_nm;
      this.prd_no = result.data[0].prd_no;
      this.prd_nm = result.data[0].prd_nm;
      this.ln_sts = result.data[0].ln_sts;
      this.use_yn = result.data[0].use_yn;
      this.dly_avg_pdn_qty = result.data[0].dly_avg_pdn_qty;
    },
    // ÏàòÏ†ïÎêú ÎÇ¥Ïö©ÏùÑ DBÏóê Ï†ÄÏû•
    async lnUpdate() {
      // ÎùºÏù∏Î™Ö Í≤ÄÏ¶ù
      if (!this.ln_nm?.trim()) {
        alert('ÎùºÏù∏Î™ÖÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // ÎùºÏù∏ÏÉÅÌÉú Í≤ÄÏ¶ù
      if (!this.ln_sts) {
        alert('ÎùºÏù∏ÏÉÅÌÉúÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // ÏÇ¨Ïö©Ïó¨Î∂Ä Í≤ÄÏ¶ù
      if (!this.use_yn) {
        alert('ÏÇ¨Ïö©Ïó¨Î∂ÄÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ Í≤ÄÏ¶ù
      if (!this.dly_avg_pdn_qty || isNaN(Number(this.dly_avg_pdn_qty))) {
        alert('ÏùºÌèâÍ∑†ÏÉùÏÇ∞ÎüâÏùÄ Ïà´ÏûêÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      // ÏÑ§ÎπÑ ÏÑ†ÌÉù Í≤ÄÏ¶ù
      for (let i = 0; i < this.lnDtlRows.length; i++) {
        if (!this.lnDtlRows[i].eqp_no) {
          alert(`${i + 1}Î≤àÏß∏ Í≥µÏ†ïÏùò ÏÑ§ÎπÑÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.`);
          return;
        }
      }
      // ÏÑúÎ≤ÑÏóê Ï†ÑÎã¨Ìï† Ï†ïÎ≥¥Î•º Í∞ùÏ≤¥Î°ú Îî∞Î°ú Íµ¨ÏÑ±
      let lnInfo = { // lnÏàòÏ†ï
        ln_nm: this.ln_nm.trim(), // ÎùºÏù∏Î™Ö
        ln_sts: this.ln_sts, // ÎùºÏù∏ÏÉÅÌÉú
        use_yn: this.use_yn, // ÏÇ¨Ïö©Ïó¨Î∂Ä
        dly_avg_pdn_qty: Number(this.dly_avg_pdn_qty) // ÏùºÌèâÍ∑†ÏÉùÏÇ∞Îüâ
      }

      let lndtlInfoArray = []; // ln_dtl ÏàòÏ†ï
      for (let i = 0; i < this.lnDtlRows.length; i++) {
        lndtlInfoArray[i] = {
          eqp_no: this.lnDtlRows[i].eqp_no, // ÏÑ§ÎπÑÎ≤àÌò∏Îßå ÏàòÏ†ïÍ∞ÄÎä•
          ln_dtl_no: this.lnDtlRows[i].ln_dtl_no // ÏàòÏ†ïÎåÄÏÉÅ
        };
      };

      let result = await axios.put(`/api/lnAndLnDtl/${this.ln_no}`, [lnInfo, lndtlInfoArray])
        .catch(err => console.log(err));

      if (result.data.message == 'ÏàòÏ†ï ÏôÑÎ£å') {
        alert('ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.');
        this.$emit('ln-reload');
        this.getLnDtlList(this.ln_no);
      } else {
        alert('ÏàòÏ†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.\nÎç∞Ïù¥ÌÑ∞Î•º ÌôïÏù∏Ìï¥Î≥¥ÏÑ∏Ïöî.');
      };
    },
    saveLn() { // Ï†ÄÏû• Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò
      if (confirm('Ï†ïÎßêÎ°ú ÏàòÏ†ïÌïòÏãúÍ≤†ÏäµÎãàÍπå?\nÎ≥ÄÍ≤ΩÎêú ÎÇ¥Ïö©ÏùÄ Ï¶âÏãú Ï†ÅÏö©Îê©ÎãàÎã§.')) {
        this.lnUpdate(); // ÏàòÏ†ïÎÇ¥Ïö© Ï†ÄÏû•
      }
    },
    // Ï∂îÍ∞Ä Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò
    addLn() {
      this.$emit("goToForm", false);
    },
    // ÏÇ≠Ï†ú Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò
    async deleteLn(lnNo) {
      if (lnNo) { // ÏÑ†ÌÉùÎêú lnÏù¥ ÏûàÏùÑ Í≤ΩÏö∞ 
        if (confirm('Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?\nÏÇ≠Ï†úÎêú Îç∞Ïù¥ÌÑ∞Îäî Î≥µÍµ¨Ìï† Ïàò ÏóÜÏäµÎãàÎã§.')) {
          let result = await axios.delete(`/api/ln/${lnNo}`)
            .catch(err => console.log(err));
          let sqlRes = result.data;
          let sqlResult = sqlRes.affectedRows;
          if (sqlResult > 0) {
            alert('Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.');
            this.$emit('ln-reload');
            this.ln_no = '';
            this.ln_nm = '';
            this.prd_no = '';
            this.prd_nm = '';
            this.ln_sts = '';
            this.use_yn = '';
            this.dly_avg_pdn_qty = '';
            this.lnDtlRows = [];
            this.$emit('clear-selection'); // ÏÑ†ÌÉùÎêú Î∂ÄÏÑú Ï¥àÍ∏∞Ìôî
          } else {
            alert('ÏÇ≠Ï†úÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
          }
        }
      } else { // ÏÑ†ÌÉùÎêú procÏù¥ ÏóÜÏùÑ Í≤ΩÏö∞
        alert("ÏÇ≠Ï†úÌï† ÎùºÏù∏ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî");
      }
    },
    resetForm() { // Ï¥àÍ∏∞Ìôî Î≤ÑÌäº ÌÅ¥Î¶≠Ïãú Ïã§ÌñâÌï† Ìï®Ïàò
      this.getLnDtlList(this.ln_no);
    },
    // ÏÑ§ÎπÑ ÏÑ†ÌÉù Î™®Îã¨ Ïó¥Í∏∞
    openEqpModal(index) {
      axios.get('/api/eqp')
        .then(res => {
          this.selectedRowIndex = index; // ÌÅ¥Î¶≠Ìïú Ïù∏Îç±Ïä§Î•º Ï†ÄÏû•
          this.eqpList = res.data;
          this.showEqpModal = true;
        })
        .catch(err => {
          console.error('ÏÑ§ÎπÑ Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®', err)
        })
    },
    handleSelectedEqp(selectedEqp) { // ÏÑ§ÎπÑ ÏÑ†ÌÉù Î™®Îã¨ÏóêÏÑú ÏÑ†ÌÉùÎêú ÏÑ§ÎπÑ
      if (this.selectedRowIndex !== null) {
        // ÌÅ¥Î¶≠Ìïú Ïù∏Îç±Ïä§Ïùò lnDtlRows ÏóÖÎç∞Ïù¥Ìä∏
        this.lnDtlRows[this.selectedRowIndex].eqp_no = selectedEqp.eqp_no;
        this.lnDtlRows[this.selectedRowIndex].eqp_nm = selectedEqp.eqp_nm;
      }
      this.showEqpModal = false;
    },
  },

}
</script>

