<template>
  <div class="container mt-4">
    <div class="row">
      <h1>ÎùºÏù∏ Í¥ÄÎ¶¨</h1>
      <!-- Ï¢åÏ∏° ÏòÅÏó≠ ÏãúÏûë -->
      <div class="col-md-5">
        <!-- Ï¢åÏ∏° Í≤ÄÏÉâ Î∞è ÌïÑÌÑ∞ ÏòÅÏó≠ ÏãúÏûë -->
        <div class="d-flex justify-content-between mb-3">
          <input type="text" class="form-control w-50" placeholder="ÎùºÏù∏Î™Ö, Ï†úÌíàÎ™Ö Í≤ÄÏÉâ..." v-model="searchQuery" />
        </div> <!-- Ï¢åÏ∏° Í≤ÄÏÉâ Î∞è ÌïÑÌÑ∞ ÏòÅÏó≠ ÎÅù -->
        <!-- Ï¢åÏ∏° Î¶¨Ïä§Ìä∏ ÏòÅÏó≠ ÏãúÏûë -->
        <div class="card p-3">
          <h4>ÎùºÏù∏ Î™©Î°ù</h4>
          <div class="table-container">
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th class="w-10">ÎùºÏù∏Î≤àÌò∏</th>
                  <th class="w-25">ÎùºÏù∏Î™Ö</th>
                  <th class="w-20">Ï†úÌíàÎ™Ö</th>
                  <th class="w-10">ÎùºÏù∏ÏÉÅÌÉú</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="ln in filteredLnList" v-bind:key="ln.ln_no" @click="selectLn(ln.ln_no)"
                  :class="{ 'table-primary': selectedLn && selectedLn.ln_no === ln.ln_no }" class="table-hover">
                  <td class="w-10">{{ ln.ln_no }}</td>
                  <td class="w-25">{{ ln.ln_nm }}</td>
                  <td class="w-20">{{ ln.prd_nm }}</td>
                  <td class="w-10">{{ lineStatusFormat(ln.ln_sts) }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div> <!-- Ï¢åÏ∏° Î¶¨Ïä§Ìä∏ ÏòÅÏó≠ ÎÅù -->
      </div> <!-- Ï¢åÏ∏° ÏòÅÏó≠ ÎÅù -->

      <!-- Ïö∞Ï∏° ÏòÅÏó≠ -->
      <lnInfo v-if="InfoView" :ln="selectedLn" @goToForm="msg" @ln-reload="getLnList" @clear-selection="clearSelection"/>
      <lnForm v-if="!InfoView" @goToInfo="msg" @ln-reload="getLnList" />
    </div>
  </div>
</template>

<script>
import CommonCodeFormat from '@/utils/useCommonCode.js'
// AJAX Î™®Îìà
import axios from 'axios';
// ÏûêÏãù Ïª¥Ìè¨ÎÑåÌä∏ import
import lnInfo from './lnInfo.vue';
import lnForm from './lnForm.vue';

export default {
  components: {
    lnInfo,
    lnForm,
  },
  data() {
    return {
      searchQuery: "", // Í≤ÄÏÉâÏñ¥ Ï¥àÍ∏∞ Í∞í
      selectedLn: null, // ÏÑ†ÌÉùÎêú ÎùºÏù∏(ÏÉÅÏÑ∏Î≥¥Í∏∞Ïóê ÌëúÏãúÎê† ÎùºÏù∏ Îç∞Ïù¥ÌÑ∞)
      lnList: [], // ÎùºÏù∏Î¶¨Ïä§Ìä∏ Îç∞Ïù¥ÌÑ∞ Îã¥ÏùÑ Î∞∞Ïó¥

      InfoView: true,
    };
  },
  computed: {
    filteredLnList() { // ÌïÑÌÑ∞Îêú lnList Î≥¥Ïó¨Ï§å
      return this.lnList.filter(ln =>
        ln.ln_nm.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
        ln.prd_nm.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
  },
  created() {
    // ÌéòÏù¥ÏßÄ Ïó¥ Îïå lnListÎç∞Ïù¥ÌÑ∞ Î∞õÏïÑÏò§Í∏∞ Ïã§Ìñâ
    this.getLnList();
  },
  methods: {
    // ÎùºÏù∏ÏÉÅÌÉú Îç∞Ïù¥ÌÑ∞ Ìè¨Î©ß Ï†ïÏùò
    lineStatusFormat(value) {
      return CommonCodeFormat.lineStatusFormat(value);
    },
    // procListÎç∞Ïù¥ÌÑ∞ Î∞õÏïÑÏò§Îäî Ìï®Ïàò
    async getLnList() {
      let result = await axios.get('/api/ln')
        .catch(err => console.log(err));
      this.lnList = result.data; // lnListÎ∞∞Ïó¥Ïóê Í≤∞Í≥ºÍ∞í Îã¥Ïùå
    },
    // ÏÉÅÏÑ∏Î≥¥Í∏∞Ïóê Î≥¥Ïó¨Ïßà Îç∞Ïù¥ÌÑ∞ Î∞õÏïÑÏò§Îäî Ìï®Ïàò
    async selectLn(lnNo) { // Î¶¨Ïä§Ìä∏ÏóêÏÑú ÏÑ†ÌÉùÌïú lnÏ†ïÎ≥¥Î•º selectedLnÏóê Ï†ÄÏû•(ÏÉÅÏÑ∏Î≥¥Í∏∞Ïóê ÌëúÏãúÎê† ÎùºÏù∏ Îç∞Ïù¥ÌÑ∞)
      try {
        const ln = this.lnList.find(ln => ln.ln_no === lnNo);
        // this.InfoView = true;Î°ú Ïª¥Ìè¨ÎÑåÌä∏Î•º ÌôúÏÑ±ÌôîÌïòÎ©¥ VueÎäî DOMÏùÑ ÏóÖÎç∞Ïù¥Ìä∏ÌïòÎäî ÏûëÏóÖÏùÑ ÏòàÏïΩ(ÎπÑÎèôÍ∏∞)
        this.InfoView = true;
        // $nextTick()ÏùÑ ÏÇ¨Ïö©ÌïòÎ©¥ DOM ÏóÖÎç∞Ïù¥Ìä∏Í∞Ä ÏôÑÎ£åÎêú ÌõÑ ÏïàÏ†ÑÌïòÍ≤å ÏûëÏóÖÏùÑ ÏàòÌñâÌï† Ïàò ÏûàÏäµÎãàÎã§.(ÎπÑÎèôÍ∏∞-> ÎèôÍ∏∞Ï≤òÎ¶¨)
        await this.$nextTick();
        this.selectedLn = ln;  // ÌÅ¥Î¶≠Ìïú ÎùºÏù∏ÏùÑ selectedLnÏóê Ï†ÄÏû•
      } catch (err) {
        console.error('ÎùºÏù∏ ÏÑ†ÌÉù Ï§ë Ïò§Î•ò Î∞úÏÉù:', err);
      }
    },
    msg(data) {
      this.InfoView = data;
      if (!data) {
        this.selectedLn = null; // lnFormÏù¥ ÌôúÏÑ±ÌôîÎêòÎ©¥ ÏÑ†ÌÉùÎêú ÎùºÏù∏ Ï¥àÍ∏∞Ìôî
      }
    },
    clearSelection() {
      this.selectedLn = null;
    },
  }
};
</script>

<style scoped>
@import '@/assets/styles/base-table.css';
</style>