<template>
  <div class="modal fade show d-block" style="background: rgba(0,0,0,0.5); z-index: 1051;" @click.self="$emit('close')">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">ÏÑ§ÎπÑ ÏÑ†ÌÉù</h5>
          <button type="button" class="btn-close" @click="$emit('close')"></button>
        </div>
        <div class="modal-body">
          <div class="d-flex justify-content-between align-items-center mb-2">
            <div class="d-flex gap-2" style="width: 450px;">
              <input type="text" class="form-control" style="width: 60%" placeholder="ÏÑ§ÎπÑÎ™Ö Í≤ÄÏÉâ" v-model="search" />
              <select class="form-select" style="width: 40%" v-model="selectedFilter">
                <option value="">Ï†ÑÏ≤¥</option>
                <option value=true>ÏÇ¨Ïö©Ï§ëÏÑ§ÎπÑ</option>
                <option value=false>ÎØ∏ÏÇ¨Ïö©ÏÑ§ÎπÑ</option>
              </select>
            </div>
            <button class="btn btn-sm btn-primary" @click="$emit('select-eqp', selectedEqp)">ÏÑ§ÎπÑ Îì±Î°ù</button>
          </div>
          <div class="table-container">
            <table class="table table-sm table-bordered text-center">
              <thead class="table-light">
                <tr>
                  <th class="w-10">ÏÑ§ÎπÑÎ≤àÌò∏</th>
                  <th class="w-20">ÏÑ§ÎπÑÎ™Ö</th>
                  <th class="w-10">ÏÑ§ÎπÑÏÉÅÌÉú</th>
                  <th class="w-20">ÏÇ¨Ïö©Ï§ëÏù∏ÎùºÏù∏</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in filteredEqp" :key="item.eqp_no" @click="toggleEqpSelection(item)"
                  :class="{ 'table-primary': isSelected(item) }" style="cursor: pointer;">
                  <td class="w-10">{{ item.eqp_no }}</td>
                  <td class="w-20">{{ item.eqp_nm }}</td>
                  <td class="w-10">{{ eqpStatusFormat(item.eqp_sts) }}</td>
                  <td class="w-20">{{ item.ln_nm }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CommonCodeFormat from '@/utils/useCommonCode.js'

export default {
  props: {
    eqpList: Array,   // Ï†ÑÏ≤¥ ÏÑ§ÎπÑ Î™©Î°ù (ÏÇ¨Ïö©Ïó¨Î∂Ä f2Îäî Ï†úÏô∏)
    selected: Array  // Ïù¥ÎØ∏ Îì±Î°ùÎêú(ÏÑ†ÌÉùÎêú) ÏÑ§ÎπÑÎ≤àÌò∏
  },
  data() {
    return {
      search: '',
      selectedEqp: null,
      selectedFilter : "",
    }
  },
  computed: {
    filteredEqp() {
      if (!this.eqpList) return [];
      return this.eqpList.filter(eqp => {
        // Í≤ÄÏÉâÏñ¥ ÌïÑÌÑ∞ÎßÅ
        const matchesSearch = !this.search || eqp.eqp_nm.toLowerCase().includes(this.search.toLowerCase());
        // ÏÇ¨Ïö©Ï§ë/ÎØ∏ÏÇ¨Ïö© ÌïÑÌÑ∞ÎßÅ
        const matchesFilter = this.selectedFilter === "" || (eqp.ln_nm ? this.selectedFilter === "true" : this.selectedFilter === "false");
        return matchesSearch && matchesFilter;
      });
    }
  },
  created() {
    this.selectedEqp = this.selected ? { eqp_no: this.selected } : null;  // ÏÑ†ÌÉù ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî(Í∏∞Ï°¥Ïóê ÏÑ†ÌÉùÎêú ÏÑ§ÎπÑÍ∞Ä ÏûàÎã§Î©¥ selectedEqpÏóê ÎÑ£Ïñ¥Ï§å)
  },
  methods: {
    // ÏÑ§ÎπÑÏÉÅÌÉú Í≥µÌÜµÏΩîÎìú Ï≤òÎ¶¨
    eqpStatusFormat(code) {
      return CommonCodeFormat.eqpStatusFormat(code);
    },
    // ÏÑ§ÎπÑ ÏÑ†ÌÉùÌïòÎ©¥ Í∑∏Í±∏ selectedEqpÏóê ÎÑ£Ïñ¥Ï£ºÎäî Î©îÏÜåÎìú (noÎûë nmÎëòÎã§)
    toggleEqpSelection(item) {
      if (item.ln_nm) {
        alert('Ïù¥ÎØ∏ ÏÇ¨Ïö© Ï§ëÏù∏ ÏÑ§ÎπÑÏûÖÎãàÎã§. ÏÑ†ÌÉùÌï† Ïàò ÏóÜÏäµÎãàÎã§.');
        return; // ÏÑ†ÌÉù Î∂àÍ∞Ä
      }
      if (this.selectedEqp && this.selectedEqp.eqp_no === item.eqp_no) {
        this.selectedEqp = null; // ÏÑ†ÌÉù Ìï¥Ï†ú
      } else {
        this.selectedEqp = { eqp_no: item.eqp_no, eqp_nm: item.eqp_nm }; // ÏÑ†ÌÉùÌïú ÏÑ§ÎπÑÎ°ú ÎåÄÏ≤¥
      }
    },
    isSelected(item) {
      return this.selectedEqp && this.selectedEqp.eqp_no === item.eqp_no;
    }
  },
}
</script>

<style scoped>
@import '@/assets/styles/base-table.css';
